const menuStyle = {
  height: '60px',
  color: 'rgb(168, 168, 168)',
  'border-top': '1px solid rgb(222, 222, 222)',
  'background-color': 'white',
  position: 'fixed',
  bottom: 0
}

export default function Menu(props) {
  return (
    <div className="wrapper" style={menuStyle}>
      <div className="container menubar">
        <div>Case 1</div>
        <div>Case 2</div>
        <div>Case 3</div>
        <div>Case 4</div>
      </div>
      <style jsx>{`
        .menubar {
          align-items: center;
          padding: 0 14px;
        }
      `}</style>
    </div>
  )
}